<template>
	<view class="box" v-if="shop.merchantShopName">
		<view class="shop-bg">
			<image lazy-load class="img" :src="shop.picUrl" mode="aspectFill"></image>
		</view>
		<view class="shop-detail">
			<image lazy-load class="logo" :src="shop.picUrl " mode="aspectFill"></image>
			<view class="shop-name">{{shop.merchantShopName}}</view>
			<view class="parent-score">
				<score :activeScore.sync='shop.score*1'></score>
			</view>
			<view class="shop-text">
				{{shop.shopDesc}}
			</view>
			<view class="connect-box">

				<view @tap="openMap(shop.lon1,shop.lat1,shop.shopAddress)" class="connect-li" :url='"/pages/goods/map?latitude="+shop.lat1+"&longitude="+shop.lon1'>
					<view class="iconfont icondizhidingwei">
					</view>
					<text class="text">{{shop.shopAddress}}</text>
				</view>
				<view class="connect-li" @tap="callPhone">
					<view class="iconfont icondianhua">
					</view>
					<text class="text">{{shop.shopManagerMobile}}</text>
				</view>
				<button open-type="share" class="connect-li no-btn">
					<view class="btn-share iconfont iconfenxiang">
					</view>
					<text class="text">分享店铺</text>
				</button>
			</view>

		</view>
		<!-- 产品详情 -->
		<view class="good-intro">
			<titleLine :title-list="[{title:'商品',id:0}]"></titleLine>
			<!-- <view class="tab-li active">商品评价</view> -->
		</view>
		<view class="good-list">
			<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :up="upOption" :down="downOption">
				<goods-list :list="data_list"></goods-list>
			</mescroll-body>
		</view>

	</view>
</template>

<script>
	let seqId = "";
	import goodsList from './components/goods-list.vue'
	import score from '../../components/score.vue'
	import titleLine from '@/components/title-line.vue'
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				data_list: [],
				upOption: {
					textNoMore: "没有更多数据",
					page: {
						size: 10
					}
				},
				downOption: {
					use: false
				},
				score: 3,
				shop: {}
			}
		},
		components: {
			score,
			goodsList,
			MescrollBody,
			titleLine
		},
		onLoad(option) {
			seqId = option.merchantShopId
			this.loadShop();

		},
		// #ifdef MP
		onShareAppMessage(res) {
			return {
				title: this.shop.merchantShopName + ',邀您一起体验！',
				path: '/pages/goods/shop?merchantShopId=' + seqId
			}
		},
		// #endif
		methods: {
			openMap(lon, lat,name) {
				if(!lon||!lat||lon==null||lat==null){
					lon=116.397486;
					lat=39.908790;
					name='天安门'
				}
				uni.openLocation({
					latitude: lat,
					longitude: lon,
					name:name,
					success: function() {
						console.log('success');
					},
					fail:function(endErr) {
						console.log(endErr);
					}
				});
			},
			// 商品列表
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				this.$api.get('/o2oSpu/findHeadPage', {
					params: {
						pCatCode: '',
						pageno: pageNum,
						pagesize: pageSize,
						merchantShopId: seqId,
						onSale: 'SALE',
						promptType: 'NONE', //促销类型
						purchaseType: 'ZC', //购买类型
					}
				}).then((res) => {
					if (res.code && res.code != 200) {
						this.mescroll.endErr();
						return
					}
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
					let curPageLen = curPageData.length;
					// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
					let totalPage = data.pages;

					//设置列表数据
					if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
					this.data_list = this.data_list.concat(curPageData)
					// 请求成功,隐藏加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					this.mescroll.endByPage(curPageLen, totalPage);
				})
			},
			// 店铺信息
			loadShop() {
				this.$api.get('/o2oMerchantShop/Echo', {
					params: {
						seqId: seqId
					}
				}).then(res => {
					if (res.code == 200) {
						this.shop = res.data;
					}
				})
			},
			// 打电话
			callPhone() {
				let self=this;
				uni.showModal({
					content: "是否拨打店铺电话" + this.shop.shopManagerMobile + "?",
					success(res) {
						if (res.confirm) {
							uni.makePhoneCall({
								phoneNumber: self.shop.shopManagerMobile,
								success: (res) => {},
								fail: (res) => {
									uni.showToast({
										title: "拨号失败，请稍后重试"
									});
									console.log(res);
								}
							});
						}
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	.connect-box {
		display: flex;
		justify-content: space-between;
		overflow: hidden;

		.connect-li {
			width: 174rpx;
			height: 30rpx;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0 14rpx;
			color: #434343;

			.iconfont {
				font-size: 26rpx;
				width: 34rpx;
				color: $color-primary;
				margin-right: 10rpx;
				background: white;

			}

			.text {
				flex-grow: 1;
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
				font-size: 20rpx;
			}

			.btn-share::after {
				border: none;

			}
		}
	}

	.shop-bg {
		background: white;
		width: 100%;
		height: 460rpx;
		position: absolute;
		z-index: 0;
		top: 0;
		left: 0;
		right: 0;
		float: left;

		.img {
			width: 100%;
			height: 100%;
		}
	}

	.shop-detail {
		background: white;
		width: 618rpx;
		background: rgba(255, 255, 255, 1);
		box-shadow: 0px 6px 8px 0px rgba(0, 0, 0, 0.1);
		border-radius: 8rpx;
		padding: 32rpx;
		margin-top: 380rpx;
		float: left;
		position: relative;
		clear: both;
		left: 50%;
		transform: translateX(-50%);
		margin-bottom: 40rpx;

		&::after {
			content: "";
			clear: both;
		}

		.logo {
			width: 108rpx;
			height: 108rpx;
			border-radius: 100%;
			position: absolute;
			left: 50%;
			top: -54rpx;
			transform: translateX(-50%);
			background: $color-primary;
		}

		.shop-name {
			width: 100%;
			text-align: center;
			margin-top: 40rpx;
			font-size: 30rpx;
			font-weight: 500;
			margin-bottom: 20rpx;
		}

		.parent-score {
			display: flex;
			justify-content: center;
		}

		.shop-text {
			font-size: 20rpx;
			font-weight: 500;
			color: rgba(153, 153, 153, 1);
			margin: 26rpx 0;
		}

		.phone-box {
			display: flex;
			justify-content: space-between;
			width: 100%;
			font-size: 20rpx;
			color: #444;
			align-items: center;

			.phone {
				display: flex;
				align-items: center;

				.iconfont {
					margin-left: 10rpx;
					color: $color-primary;
					font-size: 30rpx;
				}
			}

		}
	}

	.good-intro {
		width: calc(100% - 29px);
		margin: 0 auto;
		display: flex;
		margin: 40rpx;
		position: relative;
		z-index: 1;

		.tab-li {
			margin-right: 30rpx;
			font-size: 20rpx;
			font-weight: 500;
			position: relative;
		}

		.active {
			font-size: 26rpx;
			font-weight: bold;

			&::after {
				content: "";
				position: absolute;
				width: 90rpx;
				height: 12rpx;
				background: $color-primary;
				border-radius: 6rpx;
				left: 10rpx;
				bottom: 0;
				z-index: -1;

			}
		}
	}

	.good-list {
		width: calc(100% - 68rpx);
		margin: 0 auto;
	}
</style>
